html {
    box-sizing: border-box;
}

body {
    background: tomato;
}

* {
    margin: 0;
    padding: 0;

    &,
    &::after,
    &::before {
        box-sizing: inherit;
    }
}

img {
    max-width: 100%;
}

ul,
li {
    list-style: none;
}

#root {
    padding-top: 64px;
    display: flex;
    flex-wrap: wrap;
}

nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 64px;
    display: flex;
    width: 100%;
    background: wheat;
    z-index: 1;
}

button {
    flex-grow: 1;
    font-size: 18px;
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
